<template>
  <div>
    <b-carousel class="d-block d-md-none"
                id="carousel"
                v-model="slide"
                fade
                :interval="3500"
                label-goto-slide
                label-indicators
                indicators
                style="text-shadow: 1px 1px 2px #333;"
                @sliding-start="onSlideStart"
                @sliding-end="onSlideEnd"
    >
      <a :href="item.url" v-for="(item, index) in mobileBanners" target="_blank">
        <b-carousel-slide
            :img-src="item.image"
        ></b-carousel-slide>
      </a>
    </b-carousel>
    <b-carousel class="d-none d-md-block"
                id="carousel2"
                v-model="slide"
                fade
                :interval="3500"
                label-goto-slide
                label-indicators
                indicators
                style="text-shadow: 1px 1px 2px #333;"
                @sliding-start="onSlideStart"
                @sliding-end="onSlideEnd"
    >
      <a :href="item.url" v-for="(item, index) in pcBanners" target="_blank">
        <b-carousel-slide
            :img-src="item.image"
        ></b-carousel-slide>
      </a>
    </b-carousel>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Carousel",
  data() {
    return {
      slide: 0,
      sliding: null,

      mobileBanners: [],
      pcBanners: []
    }
  },
  created() {
    this.getBanners()
  },
  methods: {
    onSlideStart(slide) {
      this.sliding = true
    },
    onSlideEnd(slide) {
      this.sliding = false
    },
    getBanners() {
      axios.get('banner_mobile.json', {baseURL: window.location.origin})
          .then((response) => {
            this.mobileBanners = response.data;
            return response.data;
          })
          .catch((error) => {
            throw error.response.data;
          });
      axios.get('banner_pc.json', {baseURL: window.location.origin})
          .then((response) => {
            this.pcBanners = response.data;
            return response.data;
          })
          .catch((error) => {
            throw error.response.data;
          });
    }
  }
}
</script>

<style lang="scss">
#carousel {
  .img-fluid {
    height: auto;
  }

}
.carousel-indicators {
  bottom: -1rem !important;
}
@media (min-width: 1200px) {
  #carousel {
    .img-fluid {
      height: auto;
    }
  }
}
</style>
