<template>
  <div class="prize">
    <div class="container-fluid">
      <div class="row">
        <div class="col-md-6">
          <b-form @submit="onSubmit" @reset="onReset" class="p-3">
            <b-form-group id="input-group-2" label="私钥" label-for="input-1" :description="hideAccount">
              <b-form-input
                  id="input-1"
                  v-model="privateKey"
                  @input="getAccount"
                  required
                  placeholder="开奖者钱包私钥"
                  style="width: 10rem"
              ></b-form-input>
            </b-form-group>

            <b-form-group id="input-group-2" label="提前秒数" label-for="input-2"
                          description="建议设置为20,用户前端时间有误差。以及写入链需要时间。">
              <b-form-input
                  id="input-2"
                  v-model="second"
                  required
                  placeholder="开奖者钱包私钥"
              ></b-form-input>
            </b-form-group>

            <b-form-group label="奖池为空是否开奖">
              <b-form-radio-group id="radio-group-1" v-model="isPool" name="radio-pool">
                <b-form-radio :value="true">是</b-form-radio>
                <b-form-radio :value="false">否</b-form-radio>
              </b-form-radio-group>
            </b-form-group>

            <b-form-group label="单方下注是否开奖">
              <b-form-radio-group id="radio-group-2" v-model="isOnlyPool" name="radio-only-pool">
                <b-form-radio :value="true">是</b-form-radio>
                <b-form-radio :value="false">否</b-form-radio>
              </b-form-radio-group>
            </b-form-group>
            <p>开奖为{{ intervalMinute }}分钟一期. 提前{{ second }}秒进行开奖。 我的BNB： {{ bnbBalance }}</p>
            <b-button type="submit" variant="primary">手动开奖一次</b-button>
          </b-form>
          <div class="p-3">
            <hr>
            <p>当前奖池信息:</p>
            <p>注池：{{ pool }}</p>
            <p>单：{{ singlePool }}, 人数： {{ numbers(singlePool) }}</p>
            <p>双：{{ doublePool }}, 人数： {{ numbers(doublePool) }}</p>
            <p>当前期数： {{ round }}
              <timer-down ref="timerDown" @countDownEnd="countDownEnd" @timeTick="timeTick"></timer-down>
            </p>
            <!--      <p>监控合约地址： {{ contractAddress }}</p>-->
            <p style="word-break: break-all">当前最新hash： {{ lastHash }}</p>
            <p style="word-break: break-all">开奖数： {{ winningNumber }}</p>
          </div>
        </div>
        <div class="col-md-6">
          <b-form-group id="input-group-textarea-1" label="日志" label-for="textarea-auto-height" class="p-3">
            <b-form-textarea
                id="textarea-auto-height"
                placeholder="操作日志"
                rows="10"
                max-rows="40"
                v-model="prizeText"
            ></b-form-textarea>
          </b-form-group>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import Data from "@/components/Contract";
import TimerDown from "@/components/TimerDown2";
import {Transaction} from "ethereumjs-tx"
import Web3 from "web3";

export default {
  name: "Prize",
  components: {TimerDown},
  data() {
    return {
      account: '',
      privateKey: '',
      second: 23,
      lastHash: '',
      prizeText: '启动成功\r\n',
      isPool: false,
      isOnlyPool: false,
      chip: 1000, //当前每注金额
      pool: 0,
      singlePool: 0,
      doublePool: 0,
      intervalMinute: 5,
      contractAddress: Data.btcAddress,
      btcContract: {},
      tokenContract: {}, //代币合约对象
      bnbBalance: 0,
      round: 0,
    }
  },
  async created() {
    console.log('version:2021.10.18 08:20')
    this.privateKey = localStorage.getItem('privateKey');
    // window.web3 = new Web3("wss://bsc-ws-node.nariox.org")
    this.btcContract = await new web3.eth.Contract(Data.btcAbi, Data.btcAddress)
    this.tokenContract = await new web3.eth.Contract(Data.tokenAbi, Data.tokenAddress) //代币合约对象
    this.getAccount();
    this.tokenContract.events.Bet({}, (err, result) => {
      let data = result.returnValues;
      this.pool = parseFloat(web3.utils.fromWei(data.pool, 'ether'));
      this.singlePool = parseFloat(web3.utils.fromWei(data.singlePool, 'ether'))
      this.doublePool = parseFloat(web3.utils.fromWei(data.doublePool, 'ether'))
    })
    this.tokenContract.events.Prize({}, (err, result) => {
      // let data = result.returnValues;
      this.prizeText += this.formatText2Wrap('开奖成功')
      this.init()
    })
    this.init()
    this.listenEvents();
    let rd = await this.getRound();
    this.round = parseInt(rd)
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault()
      this.prize()
    },
    onReset(evt) {
      evt.preventDefault()
      // Reset our form values
      this.account = ''
      this.privateKey = ''
    },
    //获取账户余额--代币
    getBNB() {
      if (this.account === '') return;
      web3.eth.getBalance(this.account, (err, result) => {
        this.bnbBalance = this.fromWeiByEther(result)
      })
    },
    getAccount() {
      if (this.privateKey === '') return;
      let obj = web3.eth.accounts.privateKeyToAccount('0x' + this.privateKey);
      this.account = obj.address
      this.getBNB()

      localStorage.setItem('privateKey', this.privateKey)
    },
    init() {

      //获取代币余额+
      this.getBNB()

      //pool
      this.tokenContract.methods.pool().call((err, result) => {
        this.pool = this.fromWeiByEther(result)
      })

      //singlePool
      this.tokenContract.methods.singlePool().call((err, result) => {
        this.singlePool = this.fromWeiByEther(result)
      })

      //doublePool
      this.tokenContract.methods.doublePool().call((err, result) => {
        this.doublePool = this.fromWeiByEther(result)
      })

    },
    listenEvents() {
      this.btcContract.events.allEvents({}, (err, event) => {
        // console.log(event)
        // this.lastHash = event.blockHash
        this.lastHash = event.transactionHash
      })
    },
    async reloadWindow() {
      this.round++
      console.log("reload")
      console.log(this.round)
      if (this.round % 3 === 0) {
        window.location.reload();
      }
    },
    //开奖
    async prize() {
      if (this.prizeText.length > 2000) {
        this.prizeText = ""
      }
      if (this.account === '') {
        this.prizeText += '开奖失败：钱包账户为空，请填写私钥'
        return;
      }
      const rd = await this.getRound();
      const hash = this.lastHash
      if (hash === '') {
        this.prizeText += this.formatText2Wrap('开奖失败：当前hash为空')
        return;
      }
      const prizeNumber = this.winningNumber

      this.prizeText += this.formatText2Wrap('正在开奖，期数：' + rd)
      if (this.pool === 0 && this.isPool === false) {
        //奖池为空，不开奖。
        this.prizeText += this.formatText2Wrap('本期奖池为空，不开奖')
        await this.reloadWindow()
        return;
      }
      if ((this.singlePool === 0 || this.doublePool === 0) && this.isOnlyPool === false) {
        //单边注池不开
        this.prizeText += this.formatText2Wrap('单边注池为空，不开奖')
        await this.reloadWindow()
        return;
      }
      //开奖操作
      // const privateKeyBuffer = Buffer.from(this.privateKey, 'hex')

      const abiHex = web3.eth.abi.encodeFunctionCall({
        name: 'prize',
        type: 'function',
        inputs: [{
          type: 'uint256',
          name: 'rd'
        }, {
          type: 'bytes',
          name: 'hash'
        }, {
          type: 'int256',
          name: 'prizeNumber'
        }]
      }, [rd, hash, prizeNumber])
      let gasAmount = await this.tokenContract.methods.prize(rd, hash, prizeNumber).estimateGas({from: this.account})
      // console.log(gasAmount);
      const txObject = {
        from: this.account,
        to: Data.tokenAddress,
        value: '0x0',
        gas: gasAmount * 3,
        data: abiHex
      }

      this.prizeText += this.formatText2Wrap('本期hash:' + hash + ',号码：' + prizeNumber)
      this.prizeText += this.formatText2Wrap('正在进行签名交易')
      let transaction = await web3.eth.accounts.signTransaction(txObject, '0x' + this.privateKey);
      this.prizeText += this.formatText2Wrap('签名成功，正在发送开奖请求')
      let result = await web3.eth.sendSignedTransaction(transaction.rawTransaction)
      console.log(result)
      this.prizeText += this.formatText2Wrap('交易已发送，交易hash：' + result.transactionHash)
      await this.reloadWindow()


      // await web3.eth.accounts.signTransaction(txObject, '0x' + this.privateKey, (err, result) => {
      //   if (err !== null) {
      //     this.prizeText += this.formatText2Wrap('本期开奖失败：签名错误，' + err.message)
      //     return;
      //   }
      //   this.prizeText += this.formatText2Wrap('签名成功，正在发送开奖请求')
      //   const raw = result.rawTransaction;
      //   web3.eth.sendSignedTransaction(raw, (sendErr, txHash) => {
      //     if (sendErr !== null) {
      //       this.prizeText += this.formatText2Wrap('开奖异常:' + sendErr.message)
      //       return;
      //     }
      //     // console.log('txHash:', txHash)
      //     this.prizeText += this.formatText2Wrap('交易已发送，交易hash：' + txHash)
      //   })
      // });

    },
    async timeTick(seconds) {
      // console.log("second: " + seconds);
      if (seconds - this.second === 0) {
        await this.prize();
      }
    },
    async countDownEnd(result) {
      console.log("countDownEnd")
      this.round = await this.getRound();
    },
    formatNum(num) {
      return num < 10 ? '0' + num : '' + num
    },
    fromWeiByEther(num) {
      if (num) {
        return parseFloat(web3.utils.fromWei(num, 'ether'))
      }
      return 0;
    },
    formatText2Wrap(text) {
      return text + '\r\n'
    },
    formatRound(num) {
      if (num < 10) {
        return '00' + num;
      }
      if (num < 100) {
        return '0' + num
      }
      return '' + num;
    },
    async getTodayRound() {
      let time
      try {
        let res = await this.$jsonp(this.$store.state.timeApi)
        time = parseInt((res.currentTime / 1000).toString()) + 28800;
      } catch (e) {
        try {
          let res = await this.$jsonp(this.$store.state.timeApi2)
          time = parseInt((res.result.data.currentTime / 1000).toString()) + 28800;
        } catch (e2) {
          time = Math.floor(new Date().getTime() / 1000 + 28800);
        }
      }
      return Math.floor((time % 86400) / (this.intervalMinute * 60)) + 1;
    },
    async getRound() {
      let nowTime = new Date();
      let year = nowTime.getFullYear()
      let month = nowTime.getMonth() + 1
      let day = nowTime.getDate()
      let rd = await this.getTodayRound()
      return parseInt(year + this.formatNum(month) + this.formatNum(day) + this.formatRound(rd))
    },
  },
  computed: {
    //代币转USDT
    T2U() {
      return value => value / 100;
    },
    //USDT转代币
    U2T() {
      return value => value * 100;
    },
    //注池转份数
    numbers() {
      return value => value / this.chip
    },
    winningNumber() {
      let charArr = this.lastHash.split('');
      for (let i = charArr.length - 1; i >= 0; i--) {
        if (/\d/.test(charArr[i])) {
          return parseInt(charArr[i])
        }
      }
      return -1;
    },
    hideAccount() {
      if (this.account === '') return ''
      return this.account.substr(0, 6) + '...' + this.account.substr(-4, 4)
    }
  }
}
</script>

<style scoped>
.prize {
  user-select: auto;
}
</style>
