<template>
  <b-container fluid id="copyright">
    <b-row align-h="center">
      <div class="d-inline img">
        <a :href="item.url" target="_blank" v-for="(item, index) in contacts" :key="index">
          <b-img :src="item.image"></b-img>
        </a>
      </div>
    </b-row>
    <b-row align-h="center" class="copy">
      <p>{{ $t('copyright.title') }}</p>
    </b-row>
    <div class="d-flex justify-content-center align-items-center flex-wrap" style="padding-bottom: 3rem">
      <a style="color: #eee">{{ $t('web.flink') }}：</a>
      <a class="mx-2 my-1" style="color: #eee" v-for="(item, index) in links" :href="item.url" target="_blank">{{item.name}}</a>
    </div>
  </b-container>
</template>

<script>
// let links =  require('../../public/flink.json')
// console.log(links)
import axios from "axios";
export default {
  name: "Copyright",
  data() {
    return {
      links: [],
      contacts: []
    }
  },
  created() {
    this.getFlink()
    this.getContact()
  },
  methods: {
    getFlink() {
      return axios.get('flink.json', { baseURL: window.location.origin })
          .then((response) => {
            this.links = response.data;
            return response.data;
          })
          .catch((error) => {
            throw error.response.data;
          });
    },
    getContact() {
      return axios.get('contact.json', { baseURL: window.location.origin })
          .then((response) => {
            this.contacts = response.data;
            return response.data;
          })
          .catch((error) => {
            throw error.response.data;
          });
    }
  }
}
</script>

<style lang="scss">
#copyright {
  background: #000;
  padding-top: 3rem;
  padding-bottom: 1rem;

  .img {
    img {
      margin: 1rem;
    }
  }

  .copy {
    padding: .5rem;

    p {
      color: #fff;
      font-size: 1.25rem;
      line-height: 1.6;
    }
  }
}
</style>
